import React from "react";
import {CssBaseline} from "@mui/material";
import {Route, Routes} from 'react-router-dom';
import { Actors,Movies,MovieInformation,NavBar, Profile} from "./index";
import useStyles from './styles';

const App = () =>{
    const classes = useStyles();

    return(
        <div className={classes.root}>
            <CssBaseline />
            <NavBar/>
            <main className={classes.content}>
                <div className={classes.toolbar} />
                <Routes>
                    <Route exact path="/filmpire/movie/:id" element={<MovieInformation/>}>
                    </Route>
                    <Route exact path="/filmpire/actors/:id" element={<Actors/>}>
                    </Route>
                    <Route exact path="/filmpire" element={<Movies/>}>
                    </Route>
                    <Route exact path="/filmpire/approved" element={<Movies/>}>
                    </Route>
                    <Route exact path="/filmpire/profile/:id"
                           element={<Profile/>}>
                    </Route>
                </Routes>
            </main>
        </div>
    )
}
export default App;